import React from "react";
import style from "./header.module.css";
import Link from "../../Links/Link";

const NavMenu = () => {
  return (
    <>
      <div className={style.list}>
        <a href="#projects"></a>
        <a href="#gallery"></a>
        <a href="#contacts"></a>
      </div>
      <div className={style.btn}>
        <Link to={"/"} variant={"small"}>
          
        </Link>
      </div>
    </>
  );
};

export default NavMenu;
